import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import pageView from '@/components/common-ui/page-view.vue';
import addNotice from './addForm.vue';
import rightBox from '@/components/common-components/right-box.vue';
let noticeConfig = class noticeConfig extends Vue {
    constructor() {
        super(...arguments);
        this.className = '校园坐标';
        this.showBtn = false;
        this.coordinate = {
            'type': 'Point',
            'coordinates': []
        };
    }
    hidePopup(refId) {
        const component = this.$refs[refId];
        component.hide();
    }
    get noticeConfig() {
        return this.$store.state.schoolInfo.noticeConfig || [];
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    saveSchoolInfo() {
        if (this.coordinate.coordinates.length <= 0) {
            const toast = this.$createToast({
                txt: '请选择坐标',
                type: 'correct'
            });
            toast.show();
            return;
        }
        this.$store.dispatch('updateSchoolInfo', {
            schoolId: this.schoolInfo.id,
            type: 'coordinate',
            value: this.coordinate
        }).then((e) => {
            if (e.success) {
                this.showBtn = false;
                this.showToastType();
            }
            this.$store.commit('SET_SCHOOL_INFO_BY_TYPE', {
                type: 'coordinate',
                value: this.coordinate
            });
        });
    }
    mounted() {
        let that = this;
        window.addEventListener('message', function (event) {
            // 接收位置信息，用户选择确认位置点后选点组件会触发该事件，回传用户的位置信息
            var loc = event.data;
            if (loc && loc.module == 'locationPicker') { //防止其他应用也会向该页面post信息，需判断module是否为'locationPicker'
                console.log('location', loc);
                let latlng = loc.latlng;
                that.coordinate.coordinates = [latlng.lng, latlng.lat];
            }
        }, false);
    }
    showToastType() {
        const toast = this.$createToast({
            txt: '保存成功',
            type: 'correct'
        });
        toast.show();
    }
};
noticeConfig = __decorate([
    Component({
        components: {
            page,
            pageView,
            addNotice,
            rightBox
        }
    })
], noticeConfig);
export default noticeConfig;
